<template>
    <div>
        <b-row>
          <b-col sm="3"></b-col>
      <b-col sm="6">
        <b-card
          title="Open Live Account"
          class="iq-mb-3 opendemocard"
        >
        <div v-if="pagecount==1">
          <b-form>
  <b-form-group
    class="row"
    label-cols-sm="2"
    label="Type:"
    label-for="Type"
  >
    <b-form-group >
              <b-form-select plain v-model="selectaccounttype" :options="type" size="sm" class="mb-3">
                <template v-slot:first>
                  <b-form-select-option :value="null">Select</b-form-select-option>
                </template>
              </b-form-select>
              <span v-if="errors.accountTypeError" class="text-danger">Select Account Type !</span>
            </b-form-group>
  </b-form-group>
  <div v-if="selectaccounttype=='Basic'">
     <b-form-group
    class="row"
    label-cols-sm="2"
    label="Leverage:"
    label-for="Leverage"
  >
    <b-form-group >
              <b-form-select plain v-model="selectleverage" :options="leverage" size="sm" class="mb-1">
                <template v-slot:first>
                  <b-form-select-option :value="null">Select</b-form-select-option>
                </template>
              </b-form-select>
              <span v-if="errors.leverageError" class="text-danger">Select Leverage !</span>
            </b-form-group>
  </b-form-group>
   <b-form-group
    class="row"
    label-cols-sm="2"
    label="Currency:"
    label-for="Currency"
  >
    <b-form-group >
              <b-form-select plain v-model="selectcurrency" :options="currency" size="sm" class="mb-1">
                <template v-slot:first>
                  <b-form-select-option :value="null">Select</b-form-select-option>
                </template>
              </b-form-select>
              <span v-if="errors.currencyError" class="text-danger">Select Currency !</span>
            </b-form-group>
  </b-form-group>
  </div>
  </b-form>
  </div>
  <div v-if="pagecount==2">
    <table class="table table-condensed table-with-buttons">
    <tbody><tr>
        <td>Account Type</td>
                    <td>{{selectaccounttype}}</td>
            </tr>
    <tr>
        <td>Leverage</td>
        <td>{{selectleverage}}</td>
    </tr>
    <tr>
        <td>Currency</td>
        <td>{{selectcurrency}}</td>
    </tr>
        </tbody></table>
  </div>
<b-button v-if="pagecount>=2"  @click="nextpage(-1)" variant="primary">Previous</b-button>
<b-button v-if="pagecount>=2"  @click="openAccount()" variant="success" class="ml-3">Confirm</b-button>
      <b-button v-if="pagecount!=2" @click="nextpage(1)" class="ml-3" variant="primary">Continue</b-button>
        </b-card>
      </b-col>
     </b-row>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      pagecount: 1,
      selectleverage: null,
      selectaccounttype: null,
      selectcurrency: null,
      selectbalance: null,
      type: [
        { value: 'Standard', text: 'Standard' },
        { value: 'Basic', text: 'Basic' }
      ],
      leverage: [
        { value: '500', text: '1:500' },
        { value: '400', text: '1:400' },
        { value: '300', text: '1:300' },
        { value: '200', text: '1:200' },
        { value: '100', text: '1:100' }
      ],
      currency: [
        { value: 'USD', text: 'USD' }
      ],
      errors: {
        leverageError: false,
        currencyError: false,
        balanceError: false,
        accountTypeError: false
      }
    }
  },
  methods: {
    nextpage (count) {
      if (this.selectaccounttype !== null && this.selectcurrency !== null && this.selectleverage !== null) {
        this.errors.accountTypeError = false
        this.errors.currencyError = false
        this.errors.leverageError = false
        this.pagecount += count
      } else {
        if (this.selectaccounttype === null) {
          this.errors.accountTypeError = true
        } else {
          this.errors.accountTypeError = false
        }
        if (this.selectcurrency === null) {
          this.errors.currencyError = true
        } else {
          this.errors.currencyError = false
        }
        if (this.selectleverage === null) {
          this.errors.leverageError = true
        } else {
          this.errors.leverageError = false
        }
      }
    },
    openAccount () {
      const mainPassword = this.randomPassword(11, 'aA0')
      const investPassword = this.randomPassword(11, 'aA0')
      const phonePassword = this.randomPassword(11, 'aA0')
      axios.post('https://api.launchfxm.com/api_v1/create_user?Name=AbdulAziz&Leverage=' + this.selectleverage + '&Group=demoClassic/aziz&MainPassword=' + mainPassword + '&InvestPassword=' + investPassword + '&PhonePassword=' + phonePassword + '')
        .then(() => {
          this.$router.push({ name: 'account.account-overviews' })
        })
    },
    randomPassword (uzunluk, semboller) {
      let maske = ''
      if (semboller.indexOf('a') > -1) maske += 'qwertyuopasdfghjklizxcvbnm'
      if (semboller.indexOf('A') > -1) maske += 'QWERTYUOPASDFGHJKLIZXCVBNM'
      if (semboller.indexOf('0') > -1) maske += '0123456789'
      let sonuc = ''
      for (var i = uzunluk; i > 0; --i) {
        sonuc += maske[Math.floor(Math.random() * maske.length)]
      }
      return sonuc
    }
  }
}
</script>
